<template>
    <div class="flex flex-col mb-8">
        <p class="title"> Cargar lista </p>
        <div class="flex mt-4">
            <div class="flex-1">
                <input type="file" @change="load" id="file" ref="file" class="mx-auto my-5 hidden">
                <label for="file" ref="flabel"> <i class="icon icon-pencil"></i>Cargar archivo </label>
            </div>
        </div>
    </div>
    <div class="flex flex-col w-5/6 mx-auto mb-8 items-center">
        <p class="title"> Configurar vista </p>
        <p class="my-3"> Selecciona cual sera la vista principal de la tarjeta, esto tambien afectara el como se muestra el test </p>
        <div class="flex flex-col w-full md:flex-row md:space-x-2">
            <div class="flex flex-1 check">
                <input type="radio" id="esp" name="show" :value="0" v-model="showCard">
                <label for="esp"> Mostrar en español </label>
            </div>
            <div class="flex flex-1 check">
                <input type="radio" id="trad" name="show" :value="1" v-model="showCard">
                <label for="trad"> Mostrar la traducción </label>
            </div>
            <div class="flex flex-1 check">
                <input type="radio" id="mezcla" name="show" :value="2" v-model="showCard">
                <label for="mezcla"> Mostrar en cualquier idioma </label>
            </div>
        </div>
    </div>
    <div class="flex flex-col items-center">
        <p class="title"> Tarjetas a estudiar </p>
        <p class="my-3"> Selecciona la categoria que quieres que se mueste </p>
        <div class="flex-1">
            <select v-model="showCat" @change="save">
                <option value="*"> Mostrar todas las categorias </option>
                <option v-for="(c,index) in categories" :key="'cat-'+index" :value="c"> {{c}} </option>
            </select>
        </div>
    </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex'

export default {
    name: 'configView',
    data (){
        return {
            showCard: 0,
            showCat: ''
        }
    },
    computed: {
        ...mapState(['categories','show']),
    },
    watch: {
        showCard (new_){
            this.setShow([new_, this.showCat]);
        }
    },
    methods: {
        ...mapMutations(['loadWords','setShow']),
        save (){
            this.setShow([this.showCard, this.showCat]);
        },
        async load (){
            this.$refs.file.disabled = true;
            let r = new FileReader();
            r.addEventListener("loadend", () => {
                this.loadWords(r.result);
                this.$refs.file.disabled = false;
                this.$refs.flabel.innerText = "Archivo cargado";
            });

            r.readAsText(this.$refs.file.files[0]);
        }
    },
    beforeMount (){
        this.showCard = this.show[0];
        this.showCat = this.show[1];
    }
}
</script>

<style>
    .title {
        @apply text-2xl font-bold my-4;
    }
</style>
